import React from 'react';

function RecentTrades(props) {

    let [recentTradesList, setTradesList] = React.useState([]);
    let [sortColumn, setSortColumn] = React.useState('timestamp');
    let [sortDirection, setSortDirection] = React.useState('desc');
    let [userHighlight, setUserHighlight] = React.useState('');

    function TableRow(props) {
        let tradesInfo = props.tradesInfo;
        let {description, outcome, takerSide} = tradesInfo;
        let tradeTime = new Date(1970, 0, 1, 0, 0, tradesInfo.timestamp);
        let tradeString = tradeTime.toString().substring(0,24);
        let maker = tradesInfo.maker;
        let makerShort = maker.substring(0,7) + '...';
        let taker = tradesInfo.taker;
        let takerShort = taker.substring(0,7) + '...';
        let price = Math.round(tradesInfo.price*1000)/1000;
        let amount = Math.round(tradesInfo.amount);
        let rowColor = takerSide === 'Buy' ? '#E0FDD9' : '#FDE7D0';
        let makerStyle = tradesInfo.maker === userHighlight ? {color: 'red'} : {};
        let takerStyle = tradesInfo.taker === userHighlight ? {color: 'red'} : {};
        let userMouseLeave = (event) => setUserHighlight('');
        return (
            <tr style={{backgroundColor: rowColor}}>
                <td>{tradeString}</td>
                <td>{description}</td>
                <td>{outcome}</td>
                <td>{takerSide}</td>
                <td>{price}</td>
                <td>${amount}</td>
                <td style={makerStyle} onMouseOver={e => setUserHighlight(maker)} onMouseLeave={userMouseLeave}>{makerShort}</td>
                <td style={takerStyle} onMouseOver={e => setUserHighlight(taker)} onMouseLeave={userMouseLeave}>{takerShort}</td>
            </tr>
        )
    }    

    function TableHeader(props) {
        return (
            <tr>
                <td onClick={(event) => sortByColumnFunc('timestamp')}>Time</td>
                <td>Market Description</td>
                <td>Outcome</td>
                <td>Taker Order</td>
                <td onClick={(event) => sortByColumnFunc('price')}>Price</td>
                <td onClick={(event) => sortByColumnFunc('amount')}>Amount</td>
                <td>Maker</td>
                <td>Taker</td>
            </tr>
        )
    }

    function sortByColumnFunc(column) {
        if (sortColumn === column) {
            setSortDirection(sortDirection === 'desc' ? 'asc' : 'desc');
        } else {
            setSortColumn(column);
        }
    }

    React.useEffect(() => {
        async function updateTrades() {
            let apiEndpoint = 'https://x0gksbko2m.execute-api.us-east-1.amazonaws.com/prod/get-recent-trades';
            let response = await fetch(apiEndpoint);
            let responseJson = await response.json();
            let trades = responseJson.data.sort((a,b) => b.timestamp-a.timestamp);
            setTradesList(trades);
        }
        updateTrades();
        setInterval(updateTrades, 60000);
    }
    , []);

    let tableRowMap = r => <TableRow key={r.marketId} tradesInfo={r}/>;
    let tableSort = (a,b) => {
        if (sortDirection === 'desc') {
            return b[sortColumn] - a[sortColumn];
        } else {
            return a[sortColumn] - b[sortColumn];
        }
    };

    return (
        <div id='recent-trades'>
            <h3>Recent Trades</h3>
            <table id="recent-trades-table">
                <thead>
                    <TableHeader/>
                </thead>
                <tbody>
                    {recentTradesList.sort(tableSort).map(tableRowMap)}
                </tbody>
            </table>
        </div>
    )
}

export default RecentTrades;