import React from 'react';

function TopMarkets(props) {
    function TableHeader(props) {
        return (
            <tr>
                <td>Market ID</td>
                <td>Market Description</td>
                <td>Outcome</td>
                <td>Volume</td>
                <td>Last Trade</td>
                <td>Bid Price</td>
                <td>Ask Price</td>
            </tr>
        )
    }
    
    function TableRow(props) {
        let marketIdShort = props.marketInfo.marketId.substring(0,8) + '...';
        let lastTrade= new Date(1970, 0, 1, 0, 0, props.marketInfo.lastTradeTime);
        let lastTradeString = props.marketInfo.lastTradeTime ? lastTrade.toString().substring(0,24) : '';
        let bidRounded = Math.round(Number(props.marketInfo.bidPrice)*100)/100;
        let askRounded = Math.round(Number(props.marketInfo.askPrice)*100)/100;
        return (
            <tr>
                <td>{marketIdShort}</td>
                <td>{props.marketInfo.marketDescription}</td>
                <td>{props.marketInfo.outcome}</td>
                <td>{props.marketInfo.volume}</td>
                <td>{lastTradeString}</td>
                <td style={{backgroundColor: '#FDE7D0'}}>{bidRounded}</td>
                <td style={{backgroundColor: '#E0FDD9'}}>{askRounded}</td>
            </tr>
        )
    }

    // delete later once the fetch is set up 
    let sampleMarkets = [
        {marketId: '0x023489023234', marketDescription: 'foobar', outcome: 'Yes',
        volume: 23904.12, lastTradeTime: 1576822958, bidPrice: .344, askPrice: .4, liquidity: .931},
        {marketId: '0xdea49023409', marketDescription: 'test market 1', outcome: 'No',
        volume: 0, lastTradeTime: null, bidPrice: .1843, askPrice: .234, liquidity: .941},
        {marketId: '0xbeef0230482', marketDescription: 'test market 2', outcome: 'No',
        volume: 4.12, lastTradeTime: 1576812958, bidPrice: .44, askPrice: .5, liquidity: .92},
        {marketId: '0x48923998123', marketDescription: 'test market 3', outcome: 'No',
        volume: 532134, lastTradeTime: 1576802958, bidPrice: .666, askPrice: .7234, liquidity: .994},
        {marketId: '0x4890232345523', marketDescription: 'test market 4', outcome: 'Yes',
        volume: 1, lastTradeTime: 1576801958, bidPrice: .22, askPrice: .2344, liquidity: .331}
    ];
    
    let [topMarketsList, setTopMarketsList] = React.useState(sampleMarkets);


    // React.useEffect(() => {
    //     async function updateTrades() {
    //         let apiEndpoint = 'https://x0gksbko2m.execute-api.us-east-1.amazonaws.com/prod/get-recent-trades';
    //         let response = await fetch(apiEndpoint);
    //         let responseJson = await response.json();
    //         setTradesList(responseJson.data);
    //     }
    //     updateTrades();
    //     setInterval(updateTrades, 60000);
    // }
    // , [])

    let tableRows = topMarketsList.sort((a,b) => b.liquidity-a.liquidity).map(r => <TableRow key={r.marketId} marketInfo={r}/>);

    return (
        <div id='top-markets'>
            <h3>Top Markets</h3>
            <table id="top-markets-table">
                <thead>
                    <TableHeader/>
                </thead>
                <tbody>
                    {tableRows}
                </tbody>
            </table>
        </div>
    )
}

export default TopMarkets;