import React from 'react';

function HomepageStats(props) {

    let initialData = {"volume":{"24hrs":"","1wk":"","1mo":"","total":""},"volumeTrades":{"24hrs":"","1wk":"","1mo":"","total":""},"activeMarkets":{"24hrs":"","1wk":"","1mo":"","total":""},"newMarkets":{"24hrs":"","1wk":"","1mo":"","total":""},"traders":{"24hrs":"","1wk":"","1mo":"","total":""}};
    let [stats, setStats] = React.useState(initialData);

    function TableHeader(props) {
        return (
            <tr> 
                <td style={{border: 'solid black 1px'}}></td>
                <td><b>24 Hours</b></td>
                <td><b>1 Week</b></td>
                <td><b>1 Month</b></td>
                <td><b>All-time</b></td>
            </tr>
        )
    }

    function TableRow(props) {
        let rowStats = stats[props.name];
        let displayName = {'volume': 'Volume'};
        displayName['volumeTrades'] = 'Volume (Trades)';
        displayName['activeMarkets'] = 'Active Markets';
        displayName['newMarkets'] = 'New Markets';
        displayName['traders'] = 'Traders';

        return (
            <tr>
                <td><b>{displayName[props.name]}</b></td>
                <td>{rowStats['24hrs']}</td>
                <td>{rowStats['1wk']}</td>
                <td>{rowStats['1mo']}</td>
                <td>{rowStats['total']}</td>
            </tr>
        )
    }

    React.useEffect( () => {
        async function updateStats() {
            let apiEndpoint = 'https://x0gksbko2m.execute-api.us-east-1.amazonaws.com/prod/homepage-stats';
            let response = await fetch(apiEndpoint);
            let responseJson = await response.json();
            setStats(responseJson.data);
        }
        updateStats();
        setInterval(updateStats, 60000);
    }
    , []);

    return (
        <div id='homepage-stats'>
            <h3>Stats</h3>
            <table id="homepage-stats-table">
                <thead>
                    <TableHeader />
                </thead>
                <tbody>
                    <TableRow name={'volume'} />
                    <TableRow name={'volumeTrades'} />
                    <TableRow name={'activeMarkets'} />
                    <TableRow name={'newMarkets'} />
                    <TableRow name={'traders'} />
                </tbody>
            </table>
        </div>
    )
}

export default HomepageStats;